.Button{
	display: inline-block;
	padding: 10px 20px;
	border-radius: 4px;
	border: 2px solid #ccc;
	color: #000;
	margin-right: 15px;
	text-transform: uppercase;
	font-weight: bold;
	font-size: 12px;
	cursor: pointer;
}


.Button:focus{
	outline: none;
}

.Button:active{
	box-shadow: inset 2px 2px 1px rgba(0,0,0,0.3);
}

.primary {
	background: #2884f6;
	color: #fff;
}
.sucess{
	background: rgba(161,240,69,1);
}

.Button:disabled{
	background: rgba(255,255,255,0.6);
}